header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
}
nav {
  font-size: 24px;
  color: var(--main);
}

nav a {
  padding: 0 10px 0 10px;
}

@media screen and (max-width: 430px) {
  body {
    margin-left: 20px;
    margin-right: 20px;
  }

  .logo {
    width: 145px;
  }

  nav {
    font-size: 12px;
    text-transform: uppercase;
  }
}
