.banner {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px 0;
}
.banner img {
  height: 222px;
  border-radius: 25px;
}

.banner h1 {
  position: absolute;
  z-index: 10;
  color: var(--white);
  font-weight: 500;
}

.bannerCover {
  height: 100%;
  width: 100%;
  border-radius: 25px;
  position: absolute;
  z-index: 5;
  background: #000000;
  opacity: 0.3;
  mix-blend-mode: darken;
}
@media screen and (max-width: 430px) {
  .banner img {
    width: 100%;
    height: 111px;
    border-radius: 10px;
  }

  .bannerCover {
    border-radius: 10px;
  }

  .banner h1 {
    padding-left: 16px;
    font-size: 24px;
  }
}
